.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}





  .rsc-float-button {

    
    -webkit-box-shadow: 0 0 0 rgba(110,72,170, 0.4);

    
            box-shadow: 0 0 0 rgba(110,72,170, 0.4);
    -webkit-animation: pulse 1.5s infinite;
            animation: pulse 1.5s infinite 
  }

  .pulse:hover {
    -webkit-animation: none;
            animation: none;
  }
  
  @-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(110,72,170, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 30px rgba(110,72,170, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(110,72,170, 0);
    }
  }
  @keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(110,72,170, 0.4);
              box-shadow: 0 0 0 0 rgba(110,72,170, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 30px rgba(110,72,170, 0);
                box-shadow: 0 0 0 30px rgba(110,72,170, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(110,72,170, 0);
                box-shadow: 0 0 0 0 rgba(110,72,170, 0);
    }
  }  




